.btn {
	cursor: pointer;
	width: 200px;
	margin: 10px 0;
	margin-left: 10px;
	border-radius: 8px;
  	padding: 10px 24px;
	font-size:medium;
  font-family:fantasy;
	background-color: #4caf50;
	color: white;
	border-color: #4caf50;
	box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.4),
		0 6px 22px 0 rgba(0, 0, 0, 0.23);
}

.btn:hover {
	background-color: #a9ecab;
}

.btn-group {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 25px;
}

.App {
	width: 1200px;
	margin: 0 auto;
}

.container {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
}

.btn1 {
	cursor: pointer;
	padding: 5px 20px;
  	margin-left: 10px;
	border-radius: 8px;
  font-size:medium;
  font-family:fantasy;
	border-color: #f44336;
	background-color: #f44336;
	color: white;
	box-shadow: 0 8px 16px 0 rgba(2, 2, 2, 0.4),
		0 6px 22px 0 rgba(0, 0, 0, 0.23);
}
.btn1:hover {
	background-color: #ffa96f;
}

.txt{
  text-align: center;
  color: rgb(231, 179, 6);
}
