.block{
    width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid rgb(130, 212, 194);
    pad: 10px;
    margin: 10px;
    cursor: pointer;
    color: #0c5ff8;
    font-weight:bold;
    font-size: large;
    background-color: #F5DF4D;
    font-family:monospace;
}

.img{
    width: 200px;
    height:  200px;
}